<template>
  <div id="weather" v-if="featured">
    <v-layout row>
      <v-flex>
        <v-card>
          <v-spacer></v-spacer>
          <v-list-tile-content>
            <ul v-if="result">
              <img v-bind:src="`http://openweathermap.org/img/w/${result.weather[0].icon}.png`" v-bind:alt="result.weather[0].description">
              <li>
                <strong>Description:</strong> {{ result.weather[0].description }}</li>
              <li>
                <strong>Temperature:</strong> {{ result.main.temp }}&#176;F</li>
              <li>
                <strong>Humidity:</strong> {{ result.main.humidity }}</li>
              <li>
                <strong>Wind Speed:</strong> {{ result.wind.speed}} mph</li>
            </ul>
          </v-list-tile-content>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import {API} from '@/common/api';

export default {
  name: 'LighthouseWeather',
  data(){
    return {
      result: '',   
    }
  },
props: {
    featured: {
      type: Object,
      required: true
    }
  },
  computed: {
    lat () {
      return this.featured.coordinate_location.replace(/[A-Z][a-z]*/,"").replace(/\(|\)/g, "").split(' ')[1].replace(/\..*/, "");
      },
    long () {
      return this.featured.coordinate_location.replace(/[A-Z][a-z]*/,"").replace(/\(|\)/g, "").split(' ')[0].replace(/\..*/, "");
      },
    icon () {
      return `http://openweathermap.org/img/w/${result.weather[0].icon}.png`;
    }     
},
methods: {
    getWeather: function () {
      console.log('Getting weather...');
      API.get('weather', {
        params: {
          lat: this.lat,
          lon: this.long
        }
      })
      .then(response => {
        this.result = response.data
        console.log(response);
      })
      .catch(error => {
        this.errors.push(error.message)
        console.log(error);
      });
    }
  },
  mounted () {
    this.getWeather();
  }
}
</script>

<style scoped>


</style>



